@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*,
*::after,
a::before {
  box-sizing: border-box;
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  margin: 0;
}
body {
  overflow-x: hidden;
  background-color: rgba(157, 248, 255, 0.11);
  font-family: "Poppins", sans-serif;
}

::-webkit-scrollbar {
  width: 10px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #4d948e, #1cb7bd, #0d7c9e);
}

::-webkit-scrollbar-thumb:hover {
  background: #0d7c9e;
}

.fadeIn {
  -webkit-animation: fadeInComponent 1s backwards;
          animation: fadeInComponent 1s backwards;
}
@keyframes fadeInComponent {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeInComponent {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeUpComponent {
  0% {
    opacity: 0;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@-webkit-keyframes fadeUpComponent {
  0% {
    opacity: 0;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

.page {
  width: 100vw;
  min-height: 90vh;
}
.pageTitle {
  text-align: center;
  background-image: linear-gradient(rgb(81, 208, 240), rgb(30, 83, 197));
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  line-height: 2;
  width: 100%;
  font-size: 3em;
}
@media screen and (max-width: 64em) {
  .link {
    font-size: 1.4em;
  }
  .page {
    min-height: 87vh;
  }
}

.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home h1,
h2 {
  text-align: center;
  font-family: "Poppins", sans-serif;
}
.home h1 {
  font-size: 4em;
  -webkit-animation: fadeInComponent 1s 0.2s backwards;
          animation: fadeInComponent 1s 0.2s backwards;
}
.home h2 {
  margin-bottom: 2rem;
  font-size: 2em;
  -webkit-animation: fadeInComponent 1s 0.9s backwards;
          animation: fadeInComponent 1s 0.9s backwards;
}
.home button {
  padding: 0.6em 0.9em;
  border-radius: 0.3em;
  color: white;
  background: rgb(95, 197, 231);
  transition: all 0.3s;
  font-size: 2em;
  -webkit-animation: fadeInComponent 1s 1.9s backwards;
          animation: fadeInComponent 1s 1.9s backwards;
  cursor: pointer;
  overflow: hidden;
}
.home button:hover {
  background: linear-gradient(rgb(96, 167, 190), rgb(67, 115, 219));
}
.home button span {
  font-family: inherit;
  font-weight: bolder;
  display: inline-block;
  -webkit-transform: translateY(200%);
          transform: translateY(200%);
  transition: 0.3s;
}

.home button:hover span {
  -webkit-animation: fadeUpComponent 0.4s forwards;
          animation: fadeUpComponent 0.4s forwards;
  margin-left: 1rem;
}
@media screen and (max-width: 64em) {
  .home {
    min-height: 87h;
  }
}

.header {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 10vh;
  background-image: linear-gradient(rgb(96, 167, 190), rgb(67, 115, 219));
  border-bottom: 3px solid white;
}
.logo-box {
  height: 100%;
  display: grid;
  place-items: center;
}
.logo {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
.link {
  margin: 1rem;
  color: white;
  font-size: 1em;
  text-decoration: none;
}
.active {
  text-decoration: underline;
}
.header_on_portrait {
  flex-direction: column-reverse;
}

@media screen and (max-width: 64em) {
  .link {
    font-size: 1.3em;
  }
  .header {
    height: 13vh;
  }
}

.about {
  display: grid;
  place-items: center;
}
.about__main {
  width: 70vw;
  display: grid;
  place-items: center;
}
.about__main p {
  text-align: justify;
  width: 90%;
  z-index: 1;
}
.about__main img {
  z-index: 0;
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
  width: 50%;
}
.about__main__contact {
  width: 80%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 20vh;

  flex-wrap: wrap;
  margin-bottom: 10vh;
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.2);
}

.about__main__contact > * {
  display: grid;
  place-items: center;
  height: 100%;
  flex-basis: 33.3%;
}

.about__main__contact img {
  height: 10vh;
  width: 10vh;
  transition: 0.3s;
}
.about__main__contact img:hover {
  -webkit-transform: translateY(-20%);
          transform: translateY(-20%);
}
.about__main__contact-phone {
  border: khaki 3px solid;
}
.about__main__contact-whatsapp {
  border: rgb(141, 130, 31) 3px solid;
}
.about__main__contact-github {
  border: rgb(27, 26, 11) 3px solid;
}

@media screen and (max-width: 64em) {
  .about__main {
    width: 90vw;
  }
  .about__main__contact > * {
    flex-basis: 100%;
  }
}

.invalid {
  outline: red 3px solid;
}

.UI-card {
  background-image: linear-gradient(rgb(48, 112, 133), rgb(43, 83, 170));
  max-width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.UI-card form {
  max-width: 100vw;

  padding: 1.5rem;
}
.UI-card form > div {
  margin: 0.5rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.UI-card form label {
  font-weight: bolder;
  color: white;
  font-size: 1.1em;
}
.UI-card form input,
textarea {
  font-size: 1.1em;
  background: #333;
  border: none;
  color: white;
  border-radius: 0.3em;
  font-weight: bolder;
  transition: 0.3s;
  position: relative;
  padding-left: 0.3em;
}
.UI-card form input,
textarea:focus {
  box-shadow: 0 0 0.3em #333;
}
.UI-card form button {
  border-radius: 0.3em;
  font-weight: bolder;
  background-color: white;
  color: #333;
  transition: 0.3s;
  padding: 0.2em 0.2em;
}
.UI-card form button:hover {
  color: white;
  background-color: #333;
  cursor: pointer;
}

.builder__elements > div {
  display: grid;
  place-items: center;
  border-radius: 2em;
  color: white;
  font-weight: bolder;
  text-shadow: 0 0 1px white;
  cursor: grab;
  padding: 0.5em 0.7em;
}

.builder__preview {
  border-left: solid 3px rgb(55, 133, 185);
  background: white;
  overflow-y: scroll;
  overflow-x: hidden;
}

.preview__element {
  font-family: "Poppins";
}
.preview__element-active {
  outline: solid 3px red;
}

.builder__menu {
  display: grid;
  grid-template-columns: 5fr 1fr 2fr 2fr 2fr 2fr 2fr 2fr;
  grid-template-rows: 1fr;
  position: relative;
}
.builder__menu > div {
  border-right: 1px solid white;
  color: white;
  display: grid;
  place-items: center;
  text-align: center;
}
.builder__menu__element-edit-menu {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  background-image: linear-gradient(rgb(48, 112, 133), rgb(88, 129, 216));
}

.builder__menu__delete-element {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 3;
  background: rgb(14, 185, 190);
  cursor: pointer;
}
.builder__menu__step-back {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 3;
  grid-column-end: 4;
  background: linear-gradient(rgb(48, 112, 133), rgb(88, 129, 216));
  cursor: pointer;
}
.builder__menu__preview {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 4;
  grid-column-end: 5;
  background: rgb(14, 185, 190);

  cursor: pointer;
}
.builder__menu__save {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 5;
  grid-column-end: 6;
  background: linear-gradient(rgb(48, 112, 133), rgb(88, 129, 216));

  cursor: pointer;
}
.builder__menu__publish-button {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 6;
  grid-column-end: 7;
  background: rgb(14, 185, 190);
  cursor: pointer;
}
.builder__menu__delete {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 7;
  grid-column-end: 8;
  background: linear-gradient(rgb(48, 112, 133), rgb(88, 129, 216));

  cursor: pointer;
}
.builder__menu__height {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 8;
  grid-column-end: 9;
  background: rgb(14, 185, 190);

  cursor: pointer;
}
@media screen and (max-width: 64em) {
  .builder__menu {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 2fr 2fr 2fr 3fr;
    grid-template-rows: 1fr;
  }
}

.builder {
  display: grid;
  grid-template-rows: 20vh 70vh;
  grid-template-columns: 100vw;
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
}
.builder__menu {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
}
.wrapper {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}
.builder__elements {
  flex-basis: 25%;
  height: 100%;
  display: grid;
  place-items: center;
  position: relative;
}
.builder__preview {
  flex-basis: 25%;
  height: 100%;
  position: relative;
}
.long {
  flex-basis: 75%;
}
@media screen and (max-width: 64em) {
  .builder {
    grid-template-rows: 18vh 69vh;
  }
}

.websites-list {
  width: 100%;
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
@media screen and (max-width: 64em) {
  .websites-list {
    min-height: 87vh;
  }
}

.iframe-widget {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3em;
  background-image: linear-gradient(rgb(48, 112, 133), rgb(88, 129, 216));
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-direction: column;
  padding: 1em;
  margin: 1em;
  border: 3px solid #333;
  box-shadow: 0 0 1em rgba(3, 3, 3, 0.5);
}
.iframe-widget__link:link,
.iframe-widget__link:visited {
  font-weight: bolder;
  text-decoration: none;
  position: relative;
  transition: 0.3s;
  padding: 0.5em 1em;
  border-radius: 0.4em;
  background-color: rgb(23, 171, 190);
  color: white;
}

.iframe-widget__link:hover,
.iframe-widget__link:active {
  background-color: #333;
  color: white;
}
.iframe-widget iframe {
  border: 3px solid #333;
  background-color: white;
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
  position: relative;
  z-index: 1;
}

.account button {
  background-color: rgb(47, 167, 207);
  margin: 0.3rem;
  color: white;
  border-radius: 0.3em;
  padding: 0.4em 0.6em;
  transition: 0.2s;
  cursor: pointer;
}
.account button:hover {
  background-color: rgb(44, 123, 150);
}

.adminTable td {
  border: 3px solid #333;
  padding: 0.8em 1.2em;
  font-weight: bolder;
}
td.delete button {
  background-color: rgb(218, 33, 33);
}
td.delete button:hover {
  background-color: rgb(133, 42, 42);
}
.adminTable {
  border-collapse: collapse;
}

